.allpost {
  margin: 60px auto;
  width: 60%;
  display: flex;
}

.allpost_empty {
  width: 20px;
  background-color: #7a8e52;
}
.allpost_image {
  flex: 1;
  margin-left: 20px;
}

.allpost_image img {
  width: 100%;
  height: 100%;
}
.allpost_content {
  flex: 1.8;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
}
.allpost_content_author_section {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  opacity: 0.8;
}

.allpost_content_author_section button {
  border: none;
  font-weight: 600;
  padding: 5px;
}

.allpost_content_heading {
  font-size: 26px;
  font-weight: 900;
}
.allpost_content_body p {
  font-size: 16px;
  opacity: 0.8;
}

.allpost_content_body a {
  margin-left: 84%;
  opacity: 0.8;
}
