.ImpPost {
  width: 60vw;
  margin: 50px auto;
  display: flex;
}
.ImpPost_blank {
  width: 20px;
  background-color: lightseagreen;
}

.ImpPost_image {
  flex: 1;
  margin-left: 20px;
}
.ImpPost_image img {
  width: 100%;
  height: 100%;
}

.ImpPost_body {
  flex: 2;
  margin-left: 30px;
}

.ImpPost_body {
  display: flex;
  flex-direction: column;
}
.ImpPost_body_upper_header {
  display: flex;
  justify-content: space-between;
}
.ImpPost_body_upper_header button {
  border: none;
  padding: 8px;
}

.ImpPost_body_heading {
  font-size: 30px;
}
