.createpost {
  margin: 20px auto;
  width: 60%;
  display: flex;
  flex-direction: column;
}

.createpost_heading input {
  margin-top: 20px;
  width: 60%;
  height: 40px;
}

.createpost textarea {
  width: 60%;
  height: 50vh;
  margin-top: 20px;
}

.createpost button {
  margin-top: 20px;
  width: 20%;
  height: 40px;
  border: none;
  background-color: #7a8e52;
  font-weight: bold;
  font-size: 20px;
  color: #eeeeee;
}
