.post {
  display: flex;
  flex-direction: column;
  width: 65vw;
  margin: 8vh auto;
}
.post_head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.post_head h1 {
  width: 60%;
  color: #7a8e52;
}
.post_head p {
  width: 25%;
  color: #7a8e52;
}
.post_image img {
  width: 100%;
  height: 80vh;
}

.post_body {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  color: #7a8e52;
}
.post_body p {
  width: 60%;
}
.post_body div {
  margin-top: 20%;
  width: 150px;
  height: 400px;
  background-color: #7a8e52;
}
