.landing_container {
  margin: 0px;
}
.landing_navbar {
  width: 100%;
  height: 104px;
  background-color: #7a8e52;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.landing_logo a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-left: 100px;
}

.landing_login_register a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-right: 70px;
}
.landing_body {
  margin-top: 5%;
  margin-left: 100px;
  padding: 2vw;
  display: flex;
}
.landing_body div h2 {
  margin: 0px;
  font-size: 2.626vw;
  color: #7a8e52;
}

.landing_body div p {
  margin-top: 13%;
  font-size: 1vw;
  width: 80%;
  opacity: 0.75;
  color: #7a8e52;
}
.landing_body div {
  flex: 1;
}
.landing_body div img {
  margin-left: 50%;
  width: 40%;
}
